@use "global" as *;

.information {
  position: relative;
  background-color: #ddf0f1;
  padding-top: rem(91);
  padding-bottom: rem(69);
  margin-top: rem(69);
  box-shadow: 0 rem(2) rem(4) 0 rgba(0, 0, 0, 0.5);
  @include mq(md) {
    margin-top: rem(140);
    padding-top: rem(100);
    padding-bottom: rem(100);
    box-shadow: none;
  }
}

.information__inner {
  position: relative;
  margin: auto;
  @include mq(md) {
    margin: auto;
  }
}
